* {
  margin: 0;
  padding: 0;
}
body {
  box-sizing: border-box;
  font-size: 62.5%;
}

.Headerpart_wrapper__FNG-w {
  height: 100vh;
}

.Headerpart_bgimage__lXXu9 {
  background-image: url(/Rutvikshah1/TeslaClone/static/media/01_Main_Hero_Desktop.bda09151.jpg);
  height: 100vh;
  width: 100%;
  background-size: cover;
  background-position: 50% 50%;
  position: absolute;
  top: 0;
  z-index: -1;
}

nav {
  position: fixed;
  width: 100%;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  z-index: 2000;
}

ul {
  display: flex;
  justify-content: center;
  padding: 0;
  background-color: white;
  opacity: 0.5;
}

ul li {
  padding: 1px;
  margin: 1.2rem;
  list-style: none;
  display: block;
}
ul li a {
  text-decoration: none;
  font-family: 'HeiT ASC Traditional Chinese';
  font-weight: 600;
  color: black;
  font-size: 0.9rem;
}

ul li:last-child {
  position: absolute;
  right: 2rem;
}

ul li:nth-child(8) {
  position: absolute;
  right: 7rem;
}

ul li:first-child a img {
  height: 3rem;
  width: 6rem;
  position: absolute;
  left: 2rem;
  top: 0.4rem;
}

/* Text And Button  */

.Headerpart_details_wrapper__6UX_X {
  position: absolute;
  bottom: 7rem;
  left: 23%;
}

.Headerpart_information__326S2 {
  line-height: 1.6;
  font-size: 1rem;
  color: white;
  font-weight: bold;
  font-family: 'HeiT ASC Traditional Chinese';
  letter-spacing: 2px;
  margin-bottom: 1rem;
}

.Headerpart_order__GpTca {
  position: absolute;
  left: 37%;
  text-decoration: none;
  display: block;
  color: black;
  font-weight: bold;
  font-size: 1rem;
  font-family: 'HeiT ASC Traditional Chinese';
  padding: 0.8rem 0rem;
  border: 0.2rem solid white;
  width: 11rem;
  box-sizing: border-box;
  display: flex;
  background: white;
  justify-content: center;
  letter-spacing: 2px;
  -webkit-clip-path: polygon(
    0 0,
    100% 0,
    100% calc(100% - 20px),
    calc(100% - 20px) 100%,
    0 100%
  );
          clip-path: polygon(
    0 0,
    100% 0,
    100% calc(100% - 20px),
    calc(100% - 20px) 100%,
    0 100%
  );
}

.Headerpart_order__GpTca:hover {
  color: white;
  background-color: black;
  transition: all 0.3s ease-in-out;
  border: 0.2rem solid black;
}

@media (max-width: 510px) {
  .Headerpart_bgimage__lXXu9 {
    background-image: url(/Rutvikshah1/TeslaClone/static/media/01_Main_Hero_Mobile.f5ca9e2a.jpg);
    width: 100%;
  }

  ul {
    display: none;
  }

  ul li:first-child a img {
    height: 3rem;
    width: 6rem;
    left: 2rem;
    top: 0.4rem;
  }

  /* Text And Button  */

  .Headerpart_details_wrapper__6UX_X {
    position: absolute;
    bottom: 6rem;
    left: 12%;
  }

  .Headerpart_information__326S2 {
    line-height: 1.4;
    font-size: 0.8rem;
    width: 17rem;
  }

  .Headerpart_order__GpTca {
    left: 0%;
    width: 17rem;
    padding: 0.8rem 0rem;
  }
}

